<template>
  <div>
    <top></top>
    <div class="box">
        <div class="loginCenter">
             <div class="loginBg">
                <div class="loginTitle">欢迎来到礼品淘</div>
                <div class="loginDesc">我们提供赠品采购、发货、一站式服务。</div>
                <div class="inp1"><el-input prefix-icon="el-icon-mobile" placeholder="请输入手机号" v-model="account"> </el-input></div>
                <div class="inp1">
                    <el-input prefix-icon="el-icon-lock" placeholder="请输入验证码" v-model="code"> </el-input>
                    <el-button class="getCode" type="text">获取验证码</el-button>
                </div>
                <div class="inp1"><el-input prefix-icon="el-icon-lock" show-password placeholder="请设置密码" v-model="password"> </el-input></div>
                <div class="inp1"><el-input prefix-icon="el-icon-user" placeholder="请输入QQ号" v-model="qq"> </el-input></div>
                <div class="inp1"><el-input prefix-icon="el-icon-user" placeholder="请输入微信号" v-model="wx"> </el-input></div>
                <el-row :gutter="20" style="margin-top:15px;">
                     <el-col :span="12"><el-button class="submitBt" @click="register" type="primary">完成注册</el-button></el-col>
                     <el-col :span="12"><el-button class="submitBt" @click="login" type="info">返回登录</el-button></el-col>
                </el-row>  
             </div>  
        </div>  
    </div>
    <div style="position:fixed;left:0;bottom:0;width:100%;"><foot></foot></div>
  </div>
</template>

<script>

import foot from './public/foot.vue'
import top from './public/top.vue'
import {hex_md5} from "../utils/md5";
export default {
  components: {
    foot,
    top
  },
  data() {
    return {
      account:'',
      code:'',
      password:'',
      qq:'',
      wx:'',
      inviteCode:''
    };
  },
  methods:{
    register(){
      let that=this;
      if(that.account == "" || that.account  == null || that.account  == undefined){
          this.$message({
          message: '请填写账号',
          type: 'warning'
          });
      }
       if(that.code == "" || that.code  == null || that.code  == undefined){
          this.$message({
          message: '请填写验证码',
          type: 'warning'
          });
      }
       if(that.password == "" || that.password  == null || that.password  == undefined){
          this.$message({
          message: '请填写密码',
          type: 'warning'
          });
      }

     this.axios.post(process.env.API_HOST+'user/regist',
     this.qs.stringify({
          account:this.account,
          password:hex_md5(this.password),
          qq:this.qq,
          wx:this.wx,
          inviteCode:this.inviteCode
      }),{
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'domain':'taobao1.tianyalei.com'
            // 'domain':document.domain
          }
        })
    .then(function (response) {
        console.log(response)
        if(response.data.code==200){
            that.$message({
              message: '注册成功',
              type: 'success'
            });
            that.$router.push('/login')
        }else{
            that.$message({
              message: response.data.message,
              type: 'warning'
            });
        }
    })
      

    },
    login(){
      this.$router.push('/login')
    }
  }
};
</script>

<style scoped>

.loginCenter {
  background:url(../../static/loginLeft.png) no-repeat 40px center;
  height:533px;
  position:absolute;
  top:50%;
  margin-top:-266px;
  width:1200px;
  left:50%;
  margin-left:-600px;
}
.loginBg {
  background:#fff;
  border-radius: 10px;
  padding:25px;
  float:right;
  margin-top:20px;
  margin-right:20px;
  width:320px;
}
.loginTitle {
  font-size:24px;
}
.loginDesc {
  font-size:15px;
  color:#999;
}
.inp1 {
  margin-top:15px;
  position:relative;
}
.getCode {
  position:absolute;
  right:20px;
  top:0px;
}
.submitBt {
  width:100%;
}

</style>



