<template>
  <div>
    <topMember :index="0"></topMember>
    <div class="box mar">
     <div class="over">
          <div style="float:left;width:480px;">
            <div class="userTop fffbg">
                <img src="../../static/renwu.png">
                <div>
                     <div class="over"><span class="pull-left">15603723538，欢迎您！当前余额：<span class="red">￥0</span></span> <el-button class="pull-right" size="mini" type="primary" @click="$router.push({path:'/recharge'})">充值</el-button></div>
                     <div class="fontBt"><el-button @click="editInforDiv=true" type="text">编辑资料</el-button>  |<el-button @click="$router.push({path:'/cost'})" type="text">物流成本</el-button>  |<el-button @click="capitalDetail" type="text">资金明细</el-button>  |<el-button @click="editMmDiv=true" type="text">修改密码</el-button></div>
                </div>
            </div>  
            <el-row class="mar" :gutter="20">
              <el-col :span="12">
                <div class="fffbg ptkd">
                    <div class="mainH"><div class="mainHICO"></div>圆通快递</div>
                    <div class="pad15">
                        <div class="over"><span class="pull-left">区域</span><span class="pull-right">普通地区</span></div>
                        <div class="over"><span class="pull-left">首重0.5kg内</span><span class="pull-right">{{priceForm.nearPrice1/100}}元/单</span></div>
                        <div class="over"><span class="pull-left">0.5-1kg</span><span class="pull-right">{{priceForm.nearPrice2/100}}元/单</span></div>
                        <div class="over"><span class="pull-left">1kg续重 </span><span class="pull-right">{{priceForm.nearPrice3/100}}元/kg</span></div>
                    </div>                      
                </div>
              </el-col>
              <el-col :span="12">
                <div class="fffbg ptkd">
                    <div class="mainH"><div class="mainHICO"></div>服务指南</div>
                    <div class="pad15">
                         <div class="over">
                              <img style="float:left;" src="../../static/kefu.png">
                              <div style="width:140px;float:right;">
                                   <div class="fwzn">专属运营：李白</div>
                                   <div class="fwzn2">
                                     微信:17367054062<br/>Q Q:663484423
                                   </div>  
                              </div>  
                         </div>  
                         <el-row class="mar" :gutter="15">
                            <el-col :span="12"><el-button size="small" style="width:100%" type="primary">帮助中心</el-button></el-col>
                            <el-col :span="12"><el-button size="small" style="width:100%" type="primary">视频介绍</el-button></el-col>
                         </el-row>     
                    </div>                      
                </div>
              </el-col>
            </el-row>
          </div>
         <div style="float:right;width:700px;"><img src="../../static/memberBanner.png"></div>
    </div>
    <div class="mar fffbg">
       <div class="mainH pad">
          <div class="mainHICO"></div><span class="pull-left">采购任务</span>
          <el-button size="small" class="pull-right" type="primary">立即采购</el-button>
       </div>
       <div class="pad15">
         <el-table
          :data="tableData"
          border
          :header-cell-style="{background:'#f7f7f7'}"
          style="width: 100%">
          <el-table-column
            prop="id"
            label="发货任务编号"
            align="center"
            width="200">
          </el-table-column>
          <el-table-column
            prop="num"
            align="center"
            label="包裹数">
          </el-table-column>
          <el-table-column
            prop="applyTime"
            align="center"
            label="支付时间">
          </el-table-column>
          <el-table-column
            prop="money"
            align="center"
            label="支付金额">
          </el-table-column>
          <el-table-column
            prop="state"
            align="center"
            label="任务状态">
          </el-table-column>
          <el-table-column
            prop="remark"
            align="center"
            label="备注">
          </el-table-column>
          <el-table-column
            prop="orderState"
            align="center"
            label="订单状态">
          </el-table-column>
          <el-table-column
            fixed="right"
            label="操作"
            align="center"
            width="100">
            <template slot-scope="scope">
             <el-link type="primary" :underline="false">默认支付</el-link>
             <el-link type="primary" :underline="false">撤销</el-link>
            </template>
          </el-table-column>
        </el-table>
       </div>  
       <div class="pageDiv">
         <el-pagination
         background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
       </div>  
    </div>
    </div>
    <foot></foot>
      <el-dialog
      title="编辑资料"
      :visible.sync="editInforDiv"
      width="30%">
      <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="QQ">
    <el-input></el-input>
  </el-form-item>
  <el-form-item label="微信">
    <el-input></el-input>
  </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editInforDiv = false">取 消</el-button>
        <el-button type="primary" @click="editInforDiv = false">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改密码"
      :visible.sync="editMmDiv"
      width="30%">
      <el-form ref="form" :model="form" label-width="120px">
  <el-form-item label="手机号">
    <el-input></el-input>
  </el-form-item>
  <el-form-item label="验证码">
     <el-input style="width:50%"></el-input>
     <el-button type="primary">获取验证码</el-button>
  </el-form-item>
  <el-form-item label="新密码">
    <el-input show-password></el-input>
  </el-form-item>
  <el-form-item label="确认新密码">
    <el-input show-password></el-input>
  </el-form-item>
      </el-form>
      <div class="mar cen">
        <el-button @click="editMmDiv = false">取 消</el-button>
        <el-button type="primary" @click="editMmDiv = false">确 定</el-button>
      </div>
    </el-dialog>
  

  </div>
</template>

<script>
import foot from './public/foot.vue'
import topMember from './public/topMember.vue'
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      priceForm:'',
      tableData: [{
          id: 'GN20190503672150149',
          num: 1,
          applyTime: '2019-05-08 12:23:12',
          money:'5.88',
          state:'待付款',
          remark:'备注测试',
          orderState:'未发货'
        },{
          id: 'GN20190503672150149',
          num: 1,
          applyTime: '2019-05-08 12:23:12',
          money:'5.88',
          state:'待付款',
          remark:'备注测试',
          orderState:'未发货'
        }],
        currentPage: 4,
        editInforDiv:false,
        editMmDiv:false
    };
  },
  mounted(){
    if(localStorage.getItem('token')==null){
      this.$router.push({ 
        path:'/login'
      })
    }
    this.getPrice();
  },
  methods:{
     getPrice() {
      let that = this;
      this.axios
        .get(
          process.env.API_HOST+"expressPrice/my",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              "domain": "taobao1.tianyalei.com",
              "Authorization":localStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.priceForm=response.data.data;
             localStorage.setItem('priceForm',JSON.stringify(response.data.data))
          } 
        });
    },
    capitalDetail(){
      this.$router.push({ 
        path:'/capitalDetail'
      })
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
  }
};
</script>

<style scoped>
.userTop {
  padding:10px 15px;
  color:#666;
}
.userTop img {
  float:left;
  margin-right:15px;
}
.fontBt .el-button {
  padding:0px;
}
.ptkd {
  line-height: 30px;
  height:210px;
}
.fwzn {
  font-size:16px;
  font-weight: bold;
}
.fwzn2 {
  line-height:20px;
  color:#666;
  font-size:12px;
}
</style>



