<template>
    <div class="memberTopBg">
      <div class="box">
        <router-link class="memberLogo" to="/"></router-link>
        <ul class="memberMenu">
            <li><router-link v-bind:class="{ active: index==0 }" to="member">个人主页</router-link></li>
            <li><router-link v-bind:class="{ active: index==1 }" to="shop">礼品商城</router-link></li>
            <li><router-link v-bind:class="{ active: index==2 }" to="buy">立即采购</router-link></li>
            <li><router-link v-bind:class="{ active: index==3 }" to="order">订单管理</router-link></li>
            <li><router-link v-bind:class="{ active: index==4 }" to="check">查件/补发</router-link></li>
        </ul>
        <div class="memberRight">
           账户余额： 0元
           <el-button @click="$router.push({path:'/recharge'})" size="small" class="chongzhi">充值</el-button>
           <el-dropdown>
            <span class="el-dropdown-link">
              Hi~15603723538<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item><div @click="$router.push({path:'/cost'})">物流成本</div></el-dropdown-item>
              <el-dropdown-item><div @click="$router.push({path:'/capitalDetail'})">资金明细</div></el-dropdown-item>
              <el-dropdown-item>修改密码</el-dropdown-item>
              <el-dropdown-item divided><div @click="out">退出系统</div></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    
</template>

<script>
export default {
  props: {
      index: Number,
      required: true
  },
  data() {
    return {
      
    };
  },
  methods:{
    out(){
      localStorage.removeItem('token');
       this.$router.push({ 
        path:'/login'
      })
    }
  }
};
</script>

<style scoped>
.memberTopBg {
  background:#ec3440;
  height:50px;
  line-height: 50px;
  color:#fff;
  font-size:16px;
}
.memberLogo {
  display:block;
  float:left;
  height:50px;
  width:150px;
  background:url(../../../static/logoMember.png) no-repeat left center;

}
.memberMenu {
  float:left;
  width:600px;
}
.memberMenu li {
  font-size:18px;
  float:left;
  width:120px;
  text-align: center;
}
.memberMenu li a {
  color:#fff;
  display: block;
}
.memberMenu li a.active,.memberMenu li a:hover {
  background:#b51f29;
}
.memberRight {
  float:right;
  width:400px;
}
.chongzhi {
  font-size:14px;
  border:0;
  color:#ec3440;
  margin-left:10px;
  margin-right:10px;
}
.el-dropdown-link {
    cursor: pointer;
    color: #fff;
  }
  .el-icon-arrow-down {
    font-size: 14px;
  }
</style>



